import React, { useState } from 'react'
import FormErrors from './FormErrors';
import axios from 'axios'
import { toast } from 'react-toastify';
import validator from "validator"
import { useUser } from "../contexts/UserContext";
import { useIP } from "../contexts/IpContext";

function ChangeEmail(props) {

    const [new_email, setNew_email] = useState("");
    const [password, setPassword] = useState("");
    const [errors, setErrors] = useState([]);
    const { user, setUser } = useUser();
    const [isDisabled, setDisabled] = useState(false);
    const { ip } = useIP();

    const onSubmit = async event => {

        event.preventDefault();
        setDisabled(true);
        setErrors([]);
        let _errors = "";
        let email = user.email
        if (!new_email) _errors = { emailError: "*Email is required" };
        else if (!validator.isEmail(new_email)) _errors = { emailError: "*Email format is invalid" };
        if (!password) _errors = { ..._errors, pwdError: "*Password is required" };
        else if (password.length < 8) _errors = { ..._errors, pwdError: "*Password must be atleast 8 characters long" };

        if (_errors) {
            setDisabled(false);
            return setErrors(_errors);
        }

        else {
            try {
                await axios.put(`${ip}/update_email`, {

                    email,
                    new_email,
                    password
                });

                setUser({
                    ...user,
                    email: new_email
                })
                setNew_email("");
                setPassword("");

                toast.success('Email changed successfully!');

                props.setTrigger(false)
            } catch (e) {
                setErrors(e.response.data.error);
            }
        }
    }

    const reset = () => {
        setNew_email("")
        setPassword("");
        setDisabled(false);
        setErrors([]);
    }

    return (
        props.trigger &&
        <div className='LoginWrapper'>
            <div className='LoginBox changeEmail'>
                <div className='CloseBtn'>
                    <p className='close' onClick={() => { props.setTrigger(false); reset() }}>&times;</p>
                </div>
                <div className="form">
                    <h1>Change email</h1>
                    <div className="form-element">
                        <input type="email" id="newemail" placeholder="New email" onChange={(e) => setNew_email(e.target.value)} />
                        {errors.emailError &&
                            <div className='error'>
                                {errors.emailError}
                            </div>}
                    </div>
                    <div className="form-element">
                        <input type="password" id="password" placeholder="Password" onChange={(e) => setPassword(e.target.value)} />
                        {errors.pwdError &&
                            <div className='error'>
                                {errors.pwdError}
                            </div>}
                    </div>
                    <div className="form-element">
                        <button className='Btn' onClick={onSubmit}>Update</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ChangeEmail