import React, { useRef, useState, useContext, useEffect } from 'react'
import JoditEditor from 'jodit-react'
import '../styling/TextEditor.css'
import icon4 from '../icons/icon4.png'
import axios from "axios"
import IsLoggedInContext from '../contexts/IsLoggedInContext'
import { useUser } from '../contexts/UserContext'
import { toast } from 'react-toastify';
import { useIP } from "../contexts/IpContext";

function TextEditor(props) {
    const editor = useRef(null);
    const [value, setValue] = useState("");
    const { user, setUser } = useUser();
    const loginContext = useContext(IsLoggedInContext);
    const [title, setTitle] = useState(props.title);
    const [loading, setLoading] = useState(false);
    const post = props.post;
    let category = props.category;
    const { ip } = useIP();


    useEffect(() => {
        if (props.type === "editPost") {
            setValue(props.post.content)
        }
        if (props.type === "editComment") {
            setValue(props.post.content)
        }
    }, [props.type, props.post])

    const onSubmit = async event => {
        setLoading(true)
        event.preventDefault();
        let content = value;
        
        if (props.type === "comment" ? !value : !title || !value) {
            toast.error(' Enter data to be posted');
            setLoading(false)
            return;
        }

        else {
            const user_id = user.id;

            if (props.type === "comment" || props.type === "postBox") {
                try {
                    let post_id = post.id;
                    await axios.post(`${ip}/add_comment`, {
                        content,
                        post_id,
                        user_id
                    });
                    toast.success(' Your comment was made!');
                    props.setTrigger(false);
                    props.setChanges(Math.random());
                    setTitle("");
                    setValue("");
                } catch (e) {
                    setLoading(false)
                    toast.error(e.response.data.error)
                    console.log(e);
                }
            }

            else if (props.type === "editPost") {
                let pimg = extractLinksFromText(content);
                if(!pimg){
                    pimg = ""
                }
                try {
                    await axios.put(`${ip}/update_post/${post.id}/`, {
                        content,
                        title,
                        pimg
                    });
                    toast.success(' Your post was updated!');
                    props.setTrigger(false);
                    props.setChanges(Math.random());
                    setTitle("");
                    setValue("");
                } catch (e) {
                    setLoading(false)
                    toast.error(e.response.data.error)
                    console.log(e);
                }
            }
             
            else if (props.type === "editComment") {
                try {
                    await axios.put(`${ip}/update_comment/${post.id}/`, {
                        content
                    });
                    toast.success(' Your comment was updated!');
                    props.setTrigger(false);
                    props.setChanges(Math.random());
                    setValue("");
                } catch (e) {
                    setLoading(false)
                    toast.error(e.response.data.error)
                    console.log(e);
                }   
            }

            else if (props.type === "post") {

                let e = document.getElementById("categ");
                let categ = e.options[e.selectedIndex].text
                let pimg = extractLinksFromText(content);
                if(!pimg){
                    pimg = ""
                }
                try {
                    await axios.post(`${ip}/add_post`, {
                        content,
                        user_id,
                        title,
                        categ,
                        pimg
                    });
                    toast.success(' Your post was made!');
                    let total = user.total_posts + 1
                    props.setTrigger(false);
                    setUser({
                        ...user,
                        total_posts: total
                    })
                    props.setChanges(props.changes + 1)
                    setTitle("");
                    setValue("");
                } catch (e) {
                    if (e.response.data.pending) {
                        toast.warning(e.response.data.pending)
                        props.setTrigger(false);
                        setTitle("");
                        setValue("");
                    }
                    else if (e.response.data.error) {
                        toast.error(e.response.data.error)
                    }
                    setLoading(false)
                }
            }

        }
        setLoading(false)
    }

    const extractLinksFromText = (text) => {
        const regex = /<img.*?src="(.*?)"/g;
        const matches = [];
        let match;

        while ((match = regex.exec(text)) !== null) {
            matches.push(match[1]);
        }

        return matches[0];
    };

    return (
        props.trigger &&
        <div className='EditorBox'>
            <div className='CloseBtn' onClick={() => { props.setTrigger(false) }}>&times;</div>
            <div className="postUserImg">
                <img src={user.uimg ? ("data:image/jpg;base64," + user.uimg) : icon4} alt="" />
            </div>
            <div className='EditorContent'>
                <div className='categNTitle'>
                    {!post &&
                        <>
                            <select defaultValue={props.category} name="categ" id="categ">
                                {loginContext.adminLoggedIn && <option value="Announcements">Announcements</option>}
                                <option value="General">General</option>
                                <option value="Programming">Programming</option>
                                <option value="Sports">Sports</option>
                                <option value="Finances">Finances</option>
                                <option value="Photography">Photography</option>
                                <option value="Tutorials">Tutorials</option>
                                <option value="Projects">Projects</option>
                            </select>

                        </>
                    }
                    {
                        (props.type === "comment" || props.type === "postBox") ? <input type='text' placeholder='Post title' value={post.title} disabled className='editorTitleBox' style={{ color: "grey" }} />
                            :
                            <input type='text' placeholder='Post title' className='editorTitleBox'
                                value={title}
                                onChange={e => setTitle(e.target.value)} />
                    }
                </div>
                <div className='joditBox'>
                    <JoditEditor ref={editor} className='joditEditor' value={value} onChange={(content) => setValue(content)} />
                </div>
                <div className='postBtn' onClick={onSubmit}>
                    <button className='Btn' disabled={loading}>
                        <i className="far fa-paper-plane"></i>Post
                    </button>
                </div>
            </div>
        </div>
    )
}

export default TextEditor