import React from "react";
import PostIntro from "../components/PostIntro";
import { useState, useEffect, useContext } from "react";
import "../styling/Template.css";
import SideLinksBox from "../components/SideLinksBox";
import LoginSignupPopupContext from "../contexts/LoginSignupPopupContext";
import IsLoggedInContext from "../contexts/IsLoggedInContext";
import TextEditor from "./TextEditor";
import axios, { all } from 'axios';
import LoadingBar from 'react-top-loading-bar'
import { toast } from "react-toastify";
import { useIP } from "../contexts/IpContext";
import postsIntroArray from '../dummyData/postsIntroArray'
import projectArray from '../dummyData/projectArray'

export default function Template(props) {

  const [currentSort, setCurrentSort] = useState("Top");
  const { ip } = useIP();
  const [isVisible, setIsVisible] = useState(true);
  const [enableEditor, setEnableEditor] = useState(false);
  const popupsState = useContext(LoginSignupPopupContext);
  const loginContext = useContext(IsLoggedInContext);
  const [loading, setLoading] = useState(false);
  const [allPosts, setAllPosts] = useState(props.sectionName === "Projects Showcase" ? projectArray : postsIntroArray);
  const [sideLinks, setSideLinks] = useState([]);
  const [changes, setChanges] = useState(0);
  const [progress, setProgress] = useState(0);
  const [postsNumber, setPostsNumber] = useState(4)
  const [postDeleted, setPostDeleted] = useState("")
  const currentAdmin = window.localStorage.getItem("currentAdmin");
  const config = { buttons: ["bold", "italic", "underline"] }
  let sectionName = props.sectionName
  if (sectionName === "Projects Showcase") sectionName = "Projects"

  const addClass = (name) => {

    window.scrollTo(0, 0);
    document.getElementById("Top").classList.remove("currentSort");
    document.getElementById("Latest").classList.remove("currentSort");
    document.getElementById("Oldest").classList.remove("currentSort");
    document.getElementById("Popular").classList.remove("currentSort");
    if (name === "Top") {
      document.getElementById("Top").classList.add("currentSort");
    } else if (name === "Latest") {
      document.getElementById("Latest").classList.add("currentSort");
    } else if (name === "Oldest") {
      document.getElementById("Oldest").classList.add("currentSort");
    } else if (name === "Popular") {
      document.getElementById("Popular").classList.add("currentSort");
    }
  };
  let intro;
  if (props.sectionName === "Tutorials") {
    intro =
      "Explaining something is not always easy, tutorials are very useful for this. Are you good at it? Then teach our community new things!";
  } else if (props.sectionName === "Projects Showcase") {
    intro =
      "Would you like to share the progress of your project? Create a showcase and collect feedback, but above all: share your project activity!";
  } else if (props.sectionName === "Announcements") {
    intro =
      "StackNET management did some announcements, hear what they have to say for the community!";
  } else {
    intro =
      "Do you have a question that you can't solve yourself? Engage our community to find help with this. Be as clear as possible!";
  }


  const listenToScroll = () => {
    let heightToHideFrom = 180;
    const winScroll = document.body.scrollTop ||
      document.documentElement.scrollTop;

    if (winScroll > heightToHideFrom) {
      isVisible && setIsVisible(false);
    } else {
      setIsVisible(true);
    }
  };


  const getAllPosts = async (sort) => {
    setPostsNumber(8)
    setLoading(true);
    setProgress(10)
    try {
      const res = await axios.get(`${ip}/discussions/${sectionName}?sort=${sort}`);
      setProgress(70)
      setAllPosts(res.data);
      setProgress(100)
    } catch (error) {
      console.log(error.message);
      toast.error(error.message);
      setProgress(100)
    }
    setLoading(false);
  };

  const sortPostsDescendingById = (postsArray) => {
    return postsArray.sort((a, b) => b.id - a.id);
  }
  const sortPostsAscendingById = (postsArray) => {
    return postsArray.sort((a, b) => a.id - b.id);
  }
  const sortPostsByReplies = (postsArray) => {
    return postsArray.sort((a, b) => b.replyCount - a.replyCount);
  }
  const sortPostsByViews = (postsArray) => {
    return postsArray.sort((a, b) => b.viewCount - a.viewCount);
  }
  

  const getSideLinks = async () => {
    try {
      const res = await axios.get(`${ip}/get_sideLinks`);
      setSideLinks(res.data)
    } catch (error) {
      // toast.error(error.message);
    }
  };

  useEffect(() => {
    let newArr = allPosts.filter(object => {
      return object.id !== postDeleted;
    })
    setAllPosts(newArr)
  }, [postDeleted])

  useEffect(() => {
    // getAllPosts(currentSort);
    // getSideLinks();
    document.title = "StackNET - " + props.sectionName;
    window.addEventListener("scroll", listenToScroll);
  }, [currentSort, changes]);


  return (
    <div className="DiscussionBody">
      <div className="DiscussionContainer">
        <LoadingBar
          color='var(--clrprimary)'
          height={4}
          progress={progress}
          onLoaderFinished={() => setProgress(0)}
        />
        <div className="DiscussionHeading">{props.sectionName}</div>
        <div className="intro">{intro}</div>
        <div className="menuBar">
          <div className="sortBarAndRefresh">
            <div className="SortBarContainer">
              <button
                id="Top"
                onClick={() => {
                  addClass("Top");
                  setCurrentSort("Top");
                  setAllPosts(sortPostsByReplies(allPosts))
                }}
                className="sortBtn currentSort"
              >
                Top
              </button>
              <button
                id="Latest"
                onClick={() => {
                  addClass("Latest");
                  setAllPosts(sortPostsDescendingById(allPosts))
                  setCurrentSort("Latest");
                }}
                className="sortBtn"
              >
                Latest
              </button>
              <button
                id="Oldest"
                onClick={() => {
                  addClass("Oldest");
                  setAllPosts(sortPostsAscendingById(allPosts))
                  setCurrentSort("Oldest");
                }}
                className="sortBtn"
              >
                Oldest
              </button>
              <button
                id="Popular"
                onClick={() => {
                  addClass("Popular");
                  setAllPosts(sortPostsByViews(allPosts))
                  setCurrentSort("Popular");
                }}
                className="sortBtn"
              >
                Popular
              </button>
            </div>
            {isVisible
              && <div id="hide" className="refreshBtn" title="Refresh feed" onClick={() => getAllPosts(currentSort)}><i className="fas fa-arrows-rotate"></i></div>}
          </div>
          <button
            className="StartDiscussionBtn"
            onClick={loginContext.IsLoggedIn ? () => { setEnableEditor(true) } : () => { popupsState.setOpenTheLoginPopup(true) }}
            disabled={props.sectionName == "Announcements" && currentAdmin !== "true" ? true : false}
          >
            <i
              className="fa-regular fa-edit"
              style={{ color: "white", marginRight: "5px" }}
            ></i>
            {props.buttonTitle}
          </button>
        </div>
        <div className="PostsAndSideLinks">
          {!loading && (<div className="postsBox">
            <ul className="postsList">
              {allPosts.length > 0 ? allPosts.slice(0, postsNumber).map((post) => {
                return (
                  <li key={post.id}>
                    <PostIntro post={post} changes={postDeleted} setChanges={setPostDeleted} />
                  </li>
                );
              }) :
                <div className="noPosts">
                  No posts in this category yet
                  <i className="fa-regular fa-face-frown"></i>
                </div>}
              {/* <li>
                  <PostIntro postImg="https://i.imgur.com/EPpT0zL.png" />
                </li>*/}
            </ul>

            <div className="showMoreLessBtns">
              {postsNumber < allPosts.length &&
                <button className="loadMore" onClick={() => setPostsNumber(postsNumber < allPosts.length ? postsNumber + 4 : 4)}>
                  <i
                    className="fa-solid fa-chevron-down"
                    style={{ color: "var(--clrdark)", marginRight: "8px" }}
                  ></i>
                  Load more
                </button>}
              {postsNumber > 4 &&
                <button className="loadMore" onClick={() => setPostsNumber(4)}>
                  <i
                    className="fa-solid fa-chevron-up"
                    style={{ color: "var(--clrdark)", marginRight: "8px" }}
                  ></i>
                  Show less
                </button>}
            </div>
          </div>
          )}

          {/* {!loading && */}
          <div className="SideLinkContainer">
            <SideLinksBox title="Top Discussions" data={allPosts.slice(0, 5)} type="replies" />
            <SideLinksBox title="Popular Discussions" data={allPosts.slice(0, 5)} type="views" />
          </div>
          {/* } */}
        </div>

        {enableEditor &&
          <TextEditor category={sectionName} trigger={enableEditor} setTrigger={setEnableEditor} changes={changes} setChanges={setChanges} config={config} type="post" />
        }
      </div>
    </div>
  );
}
