import React, { useState, useEffect } from "react";
import axios from "axios";
import "../styling/Profile.css";
import icon4 from "../icons/icon4.png";
import PostIntro from '../components/PostIntro'
import Badge from "../components/Badge";
import ChangeEmail from "../components/ChangeEmail";
import ChangePassword from "../components/ChangePassword";
import LoadingBar from 'react-top-loading-bar'
import httpClient from "../httpClient";
import { useUser } from "../contexts/UserContext";
import { toast } from "react-toastify";
import { useParams } from "react-router-dom";
import { useIP } from "../contexts/IpContext";
import { useNavigate } from "react-router-dom";

function Profile() {

  const [changeEmail, setChangeEmail] = useState(false);
  const [changePassword, setChangePassword] = useState(false);
  const { user, setUser } = useUser();
  const [userPosts, setUserPosts] = useState("");
  const [changes, setChanges] = useState(0);
  const [disableOptions, setDisableOptions] = useState(false)
  const [profile, setProfile] = useState();
  const [progress, setProgress] = useState(0)
  const params = useParams();
  const { ip } = useIP();
  const navigate = useNavigate();


  useEffect(() => {
    getPosts()
    document.title = "StackNET - Profile"
    return () => {
      window.scrollTo(0, 0);
    }
  }, [changes, params.id]);

  useEffect(() => {
    if (params.id == user.id) {
      setDisableOptions(false)
      setProfile(user)
    }
    else {
      setDisableOptions(true)
      getUser();
    }
  }, [user, params.id])

  const uploadPicture = async (id, image) => {
    try {
      const formData = new FormData();
      formData.append('file-input', image);
      const res = await axios.post(`${ip}/upload_pic/${id}`, formData);
      console.log(res.data);
    } catch (error) {
      console.log(error);
    }
  };

  const getPosts = async () => {
    try {
      setProgress(30);
      const res = await axios.get(`${ip}/get_posts?id=${params.id}`);
      setUserPosts(res.data);
      setProgress(100);
    } catch (error) {
      // alert(error);
    }
  };

  const getUser = async () => {
    try {
      const res = await axios.get(`${ip}/u/profilee/${params.id}`);
      setProfile(res.data) && setProfile(res.data);;
    } catch (e) {
      toast.error(e.response.data.error)
      navigate('/')
    }
  }

  const updateBio = async () => {
    let id = profile.id
    let bio = profile.bio
    try {
      const res = await httpClient.put(`${ip}/updateBio`, { id, bio });
    if (params.id == user.id) {
      setUser({ ...user, bio: bio })
    }
      console.log(res.data);
    } catch (error) {
      console.log(error);
    }
  };

  function handleChange(e) {
    uploadPicture(user.id, e.target.files[0]);
    window.location.reload(false);
  }

  return (
    <div className="ProfileBody">
      {profile &&
        <div className="ProfileContainer">
          <div className="ProfileBox">
            <LoadingBar
              color='var(--clrprimary)'
              height={4}
              progress={progress}
              onLoaderFinished={() => setProgress(0)}
            />
            <div className="Avatar">
              {user.id == params.id &&
                <input
                  type="file"
                  name="file-input"
                  id="file-input"
                  accept=".jpeg, .png, .jpg"
                  className="file-input__input"
                  onChange={handleChange} />
              }
              <div className="file-input">
                <label className="file-input__label" htmlFor="file-input">
                  <div className="image">
                    {user.id == params.id &&
                      <div className="asd">
                        <i className="fa-solid fa-pencil " style={{ color: "white" }}></i>
                      </div>
                    }
                    <div className="userImg" style={{ backgroundImage: `url(${profile.uimg ? ("data:image/jpg;base64," + profile.uimg) : icon4})` }}></div>
                  </div>
                </label>
              </div>
            </div>
            <div className="ProfileBoxContent">
              <h1>{profile.fullname && profile.fullname}</h1>
              <div className="info">
                <span>
                  <i className="fa-solid fa-envelope" style={{ color: "white" }}></i>{" "}
                  {profile.email && profile.email}
                </span>
                <span>
                  <i
                    className="fa-solid fa-person-half-dress"
                    style={{ color: "white" }}
                  ></i>{" "}
                  {profile.gender && profile.gender}
                </span>
                <span>
                  <i
                    className="fa-solid fa-clipboard-check"
                    style={{ color: "white", marginRight: "3px" }}
                  ></i>
                  {profile.date_joined && profile.date_joined.slice(0, 10)}
                </span>
              </div>
              <div className="Badges">
                <Badge badgeName="New member" />
                {userPosts.length > 0 && <Badge badgeName="Start a discussion" />}
                {/* <Badge badgeName="First comment" /> */}
              </div>
              <textarea
                className="bio"
                placeholder="Write something about yourself ..."
                maxLength="250"
                onChange={(e) => setProfile({ ...profile, bio: e.target.value })}
                onBlur={updateBio}
                disabled={disableOptions}
                value={profile.bio}
              />
            </div>
          </div>

          <div className="profileOptions">
            <div className="content">
              <input type="radio" name="slider" id="home" />
              <input type="radio" name="slider" id="blog" />
              <div className="list">
                <label htmlFor="home" className="home">
                  <i className="far fa-comment"></i>
                  <span className="title" onClick={() => getPosts()}>Posts ({userPosts.length})</span>
                </label>
                {!disableOptions &&
                  <label htmlFor="blog" className="blog">
                    <i className="fas fa-sliders"></i>
                    <span className="title">Settings</span>
                  </label>
                }
              </div>
              <div className="text-content">
                <div className="home text">
                  <ul className="postsList">
                    {userPosts && userPosts.map((post) => {
                      return (
                        <li key={post.id}>
                          <PostIntro post={post} changes={changes} setChanges={setChanges} />
                        </li>
                      );
                    })}
                  </ul>
                </div>
                <div className="blog text">

                  {!disableOptions &&
                    <div className="profileSettings">
                      <h2>Account</h2>
                      <div className="settingsBtns">
                        <button className="settingBtn loginBtn" onClick={() => { setChangeEmail(true) }}>Change email</button>
                        <button className="settingBtn loginBtn" onClick={() => { setChangePassword(true) }}>Change password</button>
                      </div>
                    </div>
                  }
                </div>
              </div>
            </div>
          </div>
        </div>
      }
      <ChangeEmail trigger={changeEmail} setTrigger={setChangeEmail} />
      <ChangePassword trigger={changePassword} setTrigger={setChangePassword} />
    </div>
  );
};

export default Profile;
