import React, { useState } from "react";
import icon4 from "../icons/icon4.png";
import "../styling/PostBox.css";
import axios from "axios";
import { toast } from 'react-toastify'
import { useUser } from "../contexts/UserContext";
import { Link } from "react-router-dom";
import { useIP } from "../contexts/IpContext";

function PostBox(props) {

  const currentAdmin = window.localStorage.getItem("currentAdmin");
  const { user } = useUser();
  const monthNames = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
  const [dropDown, setDropDown] = useState(false);
  const { ip } = useIP();
  let date = new Date(props.postedOn)

  const toggleDropDown = () => {
    if (dropDown === true) {
      setDropDown(false);
    } else {
      setDropDown(true);
    }
  };

  const updateComment = () => {
    props.setCommentID(props.id);
    props.setType("editComment");
    props.trigger(true);
    props.setCommentContent(props.comment);
  }
  const deleteComment = async () => {

    const id = props.id

    if (window.confirm("Are you sure you want to delete this comment?") === true) {

      try {

        await axios.delete(`${ip}/delete_comment/${id}/`);
        toast.success('Comment deleted successfully!');
        props.setChanges(id)

      } catch (e) {
        alert(e);
      }
    }
  }

  return (
    <div className="postBoxContainer">
      <div className="postUserImg">
        <img src={props.uimg ? ("data:image/jpg;base64," + props.uimg) : icon4} alt="" />
      </div>
      <div className="nameAndDescWrapper">
        <div className="postBoxContent">
          {/* {props.title && <div className="Title"><h2>{props.title}</h2>
        </div>} */}

          <div className="wrapperDiv">
            <div className="nameAndDate">
              <Link to={`/u/profile/${props.uid}`}>
                <div className="postedByName" title="Visit profile">{props.authorName}</div>
              </Link>
              <div className="postedDate"><i className="fa-solid fa-pen-to-square"></i>{`${monthNames[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`}</div>
            </div>

            {!props.title &&
              <div className="RepliesNIcon">
                {(user.id === props.uid || currentAdmin == "true") &&
                  <div className="PanelBtnNProfile toggle">
                    <div className="profile" onClick={toggleDropDown}>
                      <p>
                        <i
                          className="fa-solid fa-ellipsis"
                          style={{ marginBottom: "10px" }}
                        ></i>
                      </p>
                    </div>
                  </div>}

                {dropDown && (
                  <div className="dropdown delete commentDelete">
                    <div className="sub-item" onClick={() => { setDropDown(false); deleteComment() }}>
                      <i
                        className="fa-solid fa-trash"
                        style={{ color: "var(--clrprimary)" }}
                      ></i>
                      Delete
                    </div>
                    {user.id === props.uid &&
                      <div className="sub-item" onClick={() => { setDropDown(false); updateComment() }}>
                        <i
                          className="fa-solid fa-pen-to-square"
                          style={{ color: "var(--clrprimary)" }}
                        ></i>
                        Edit
                      </div>}
                  </div>
                )}

              </div>
            }
          </div>


        </div>
        <div className="postBoxDesc" dangerouslySetInnerHTML={{ __html: props.desc }}>
        </div>
      </div>
    </div>
  );
}
PostBox.defaultProps = {
  desc: "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Vero a, cupiditate harum molestiae laboriosam illum laudantium voluptate, error quisquam sint id quia ullam quidem iusto! Ipsum laudantium aliquam.",
  authorName: "Jasper",
  postedOn: "20 Dec, 2022"
};
export default PostBox;
