import React, { useEffect, useContext, useState } from "react";
import { Link } from "react-router-dom";
import PostBox from "../components/PostBox";
import "../styling/Post.css";
import LoginSignupPopupContext from "../contexts/LoginSignupPopupContext";
import IsLoggedInContext from "../contexts/IsLoggedInContext";
import icon4 from '../icons/icon4.png'
import { useParams } from "react-router-dom";
import TextEditor from "../components/TextEditor"
import axios from "axios";
import { useUser } from "../contexts/UserContext";
import { toast } from 'react-toastify';
import { useIP } from "../contexts/IpContext";
import { useNavigate } from "react-router-dom";

function Post() {
  const popupsState = useContext(LoginSignupPopupContext);
  const loginContext = useContext(IsLoggedInContext);
  const [enableEditor, setEnableEditor] = useState(false);
  const [post, setPost] = useState("");
  const [comments, setComments] = useState([]);
  const config = { buttons: ["bold", "italic", "underline"] }
  const { user } = useUser();
  const param_id = useParams();
  const [changes, setChanges] = useState("")
  const [postUpdated, setPostUpdated] = useState("")
  const [commentUpdated, setCommentUpdated] = useState("")
  const [type, setType] = useState("");
  const navigate = useNavigate();
  const currentAdmin = window.localStorage.getItem("currentAdmin");
  const [editPost, setEditPost] = useState(null)
  const [commentID, setCommentID] = useState("")
  const { ip } = useIP();
  const [commentContent, setCommentContent] = useState("")

  const fetchPost = async () => {
    try {
      const res = await axios.get(`${ip}/post/${param_id.id}`);
      if (res.data[0].status === "pending" && currentAdmin === "false") {
        toast.error('This post is currently unavailable!');
        navigate("/")
      }
      else {
        setPost(res.data[0]);
        setComments(res.data[1]);
        document.title = "StackNET - " + res.data[0].title
      }
    }
    catch (e) {
      toast.error('Invalid URL');
      navigate("/")
    }
  };

  const update_comments = async () => {
    try {
      const res = await axios.get(`${ip}/post/${param_id.id}`);
      setComments(res.data[1]);
    }
    catch (e) {
      toast.error('Invalid URL');
      navigate("/")
    }
  };


  const deletePost = async event => {
    if (window.confirm("Are you sure you want to delete this post?") === true) {

      try {
        await axios.delete(`${ip}/delete_post/${param_id.id}/`);

        toast.success('Post deleted successfully!');
        navigate(-1)
      } catch (e) {
        alert(e.response.data.error);
      }
    }
  }

  const approvePost = async event => {

    event.preventDefault();
    const id = post.id
    if (window.confirm("Are you sure you want to approve this post?") === true) {

      try {
        await axios.put(`${ip}/approve_post/${id}/`);

        toast.success('Post approved successfully!');

      } catch (e) {
        alert(e.response.data.error);
      }
    }
  }

  useEffect(() => {
    let newArr = comments.filter(object => {
      return object.id !== changes;
    })
    setComments(newArr);
  }, [changes])

  useEffect(() => {
    update_comments();

  }, [commentUpdated])

  useEffect(() => {
    window.scrollTo(0, 0);
    setChanges("")
    fetchPost();
  }, [param_id.id, postUpdated]);

  return (
    <>
      {post &&
        <div className="postPage">
          <div className="post_Title">
            <Link to={`/discussions/${post.categ}`}>
              <div className="sectionName">
                <i
                  className="fa-regular fa-comments"
                  style={{ color: "rgb(113, 143, 151)", marginRight: "5px" }}
                ></i>

                {post.categ}
              </div>
            </Link>
            {post.title && (
              <h1>{post.title}</h1>
            )}
          </div>
          <div className="postBody">
            <div className="postContainer">
              <div className="PostsAndSide">
                <div className="posts">
                  <PostBox desc={post.content} authorName={post.fullname} postedOn={post.date} uimg={post.uimg} title={post.title} uid={post.user_id} />
                  {/* title={post.title} */}
                  {!loginContext.IsLoggedIn ? (
                    <div className="notloggedinbox">
                      <div>
                        <p>
                          <i
                            className="fa-regular fa-comments"
                            style={{ marginRight: "5px" }}
                          ></i>
                          Contribute to this discussion?
                        </p>
                        <button
                          className="btnBlue"
                          onClick={() => popupsState.setOpenTheLoginPopup(true)}
                        >
                          <i
                            className="fa-sharp fa-solid fa-arrow-right-to-bracket"
                            style={{
                              color: "var(--bgdark)",
                              marginRight: "5px",
                              paddingTop: "2px",
                            }}
                          ></i>
                          Sign in
                        </button>
                        or
                        <button
                          className="btnBlue"
                          onClick={() => popupsState.setOpenTheSignupPopup(true)}
                        >
                          <i
                            className="fas fa-user-plus Button-icon"
                            style={{
                              color: "var(--bgdark)",
                              marginRight: "5px",
                              paddingTop: "2px",
                            }}
                          ></i>
                          Create an account
                        </button>
                      </div>
                    </div>
                  ) :
                    post.status !== "pending" &&
                    <div className="replytopostdiv postBoxContainer" onClick={() => { setType("postBox");  setEnableEditor(true) }}>
                      <div className="postUserImg">
                        <img src={user.uimg ? ("data:image/jpg;base64," + user.uimg) : icon4} alt="" />
                      </div>
                      {/* <i className="fas fa-user"></i> */}
                      <div className="WriteComment">Write a comment...</div>
                    </div>
                  }
                  {comments && comments.map((comment) => {
                    return (
                      <PostBox category={post.categ} key={comment.id} comment={comment} setCommentContent={setCommentContent} desc={comment.content} setCommentID={setCommentID} setType={setType} trigger={setEnableEditor} authorName={comment.fullname} postedOn={comment.date} uimg={comment.uimg} uid={comment.user_id} id={comment.id} changes={changes} setChanges={setChanges} />
                    );
                  })}

                </div>
                <div className="sideOptions">
                  {post.status !== "pending" &&
                    <button className="postReply" onClick={loginContext.IsLoggedIn ? () => { setType("comment"); setEnableEditor(true); setEditPost(false) } : () => popupsState.setOpenTheLoginPopup(true)}>
                      <i className="fa-solid fa-comment"></i>
                      <span className="postBtnName">Comment</span>
                    </button>}

                  {(currentAdmin === "true" && post.status === "pending") &&
                    <button className="postReply" onClick={approvePost}>
                      <i className="fa-solid fa-check"></i>
                      <span className="postBtnName">Approve post</span>
                    </button>
                  }

                  {(user.id === post.user_id || currentAdmin === "true") &&
                    <button className="postReply" onClick={() => { loginContext.IsLoggedIn ? deletePost() : popupsState.setOpenTheLoginPopup(true) }}>
                      <i className="fa-solid fa-trash"></i>
                      <span className="postBtnName">Delete post</span>
                    </button>
                  }

                  {(user.id === post.user_id) &&
                    <button className="postReply" onClick={() => { setType("editPost"); setEditPost(true); setEnableEditor(true) }}>
                      <i className="fa-solid fa-pen-to-square"></i>
                      <span className="postBtnName">Edit post</span>
                    </button>
                  }

                </div>
              </div>
            </div>
          </div>
          <div className="commentBox">
            {(enableEditor && type === "editPost") &&
              <TextEditor post={post} title={post.title} config={config} trigger={enableEditor} setChanges={setPostUpdated} setTrigger={setEnableEditor} type={type} />
            }
            {(enableEditor && type === "comment") &&
              <TextEditor title={post.title} post={post} config={config} setChanges={setCommentUpdated} trigger={enableEditor} setTrigger={setEnableEditor} type={type} />
            }
            {(enableEditor && type === "editComment") &&
              <TextEditor post={commentContent} title={post.title} config={config} trigger={enableEditor} setChanges={setCommentUpdated} setTrigger={setEnableEditor} type={type} />
            }
            {(enableEditor && type === "postBox") &&
              <TextEditor title={post.title} post={post} config={config} setChanges={setCommentUpdated} trigger={enableEditor} setTrigger={setEnableEditor} type={type} />
            }

          </div>
        </div>
      }
    </>
  );
}

export default Post;
