import React, { useState } from 'react'
import FormErrors from './FormErrors';
import axios from 'axios'
import { toast } from 'react-toastify';
import { useUser } from "../contexts/UserContext";
import { useIP } from "../contexts/IpContext";

function ChangePassword(props) {

    const [password, setPassword] = useState("");
    const [new_password, setNew_password] = useState("");
    const [passwordAgain, setPasswordAgain] = useState("");
    const [errors, setErrors] = useState([]);
    const [isDisabled, setDisabled] = useState(false);
    const { user } = useUser();
    const { ip } = useIP();

    const onSubmit = async event => {

        event.preventDefault();
        setDisabled(true);
        let email = user.email
        setErrors([]);
        let _errors = [];
        if (!password) _errors = { ..._errors, pwdError: "*Password is required" };
        if (!new_password) _errors = { ..._errors, newPwdError: "*New password is required" };
        if (!passwordAgain) _errors = { ..._errors, cPwdError: "*Password is required" };
        else if (password.length < 8) _errors = { ..._errors, pwdError: "*Password must be atleast 8 characters long" };
        else if (passwordAgain.length < 8) _errors = { ..._errors, cPwdError: "*Password must be atleast 8 characters long" };
        else if (new_password.length < 8) _errors = { ..._errors, newPwdError: "*Password must be atleast 8 characters long" };
        if (password === new_password) _errors = { ..._errors, newPwdError: "*New password cannot be your current password" };
        else if (new_password !== passwordAgain) _errors = { ..._errors, cPwdError: "*Passwords do not match" };

        if (_errors) {
            setDisabled(false);
            return setErrors(_errors);
        }

        else {
            try {
                await axios.put(`${ip}/update_password`, {

                    email,
                    password,
                    new_password
                });

                reset();
                toast.success('Password changed successfully!');

                props.setTrigger(false)
            setDisabled(false);

            } catch (e) {
                setDisabled(false);
                setErrors(e.response.data.error);
            }
        }
    }

    const reset = () => {
        setPassword("");
        setPasswordAgain("");
        setNew_password("");
        setErrors([]);
        setDisabled(false);

    }

    return (
        props.trigger &&
        <div className='LoginWrapper'>
            <div className='LoginBox changeEmail'>
                <div className='CloseBtn'>
                    <p className='close' onClick={() => { props.setTrigger(false); reset(); }}>&times;</p>
                </div>
                <div className="form">
                    <h1>Change password</h1>
                    <div className="form-element">
                        <input type="password" id="currentpassword" placeholder="Current password" onChange={(e) => setPassword(e.target.value)} />
                        {errors.pwdError &&
                            <div className='error'>
                                {errors.pwdError}
                            </div>}
                    </div>
                    <div className="form-element">
                        <input type="password" id="newpassword" placeholder="New password" onChange={(e) => setNew_password(e.target.value)} />
                        {errors.newPwdError &&
                            <div className='error'>
                                {errors.newPwdError}
                            </div>}
                    </div>
                    <div className="form-element">
                        <input type="password" id="confirmnewpassword" placeholder="Confirm new password" onChange={(e) => setPasswordAgain(e.target.value)} />
                        {errors.cPwdError &&
                            <div className='error'>
                                {errors.cPwdError}
                            </div>}
                    </div>
                    <div className="form-element">
                        <button className='Btn' onClick={onSubmit}>Update</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ChangePassword