import Navbar from "./components/Navbar";
import {
  BrowserRouter as Router,
  Route,
  Routes
} from "react-router-dom";
import Discussions from "./pages/Discussions";
import Projects from "./pages/Projects";
import Tutorials from "./pages/Tutorials";
import Home from "./pages/Home";
import Footer from "./components/Footer";
import Post from "./pages/Post";
import LoginSignupPopupState from "./contexts/LoginSignupPopupState";
import SignupPopup from "./components/SignupPopup";
import LoginPopup from "./components/LoginPopup";
import Profile from "./pages/Profile";
import Sections from "./pages/Sections";
import AdminPanel from "./pages/AdminPanel";
import AllUsers from "./components/AllUsers";
import ApprovePosts from "./components/ApprovePosts";
import PrivacyPolicy from "./pages/PrivacyPolicy";
import IsLoggedInState from './contexts/IsLoggedInState'
import { UserContextState } from "./contexts/UserContext";
import { IPcontextState } from "./contexts/IpContext";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Navigate } from "react-router-dom";

function App() {

  if(!localStorage.getItem("darkmode")){
    window.localStorage.setItem("darkmode", false);
  }
  if (!localStorage.getItem("currentAdmin")) {
    window.localStorage.setItem("currentAdmin", false);
  }
  return (
    <>
      <IsLoggedInState>
        <LoginSignupPopupState>
          <UserContextState>
            <IPcontextState>
            <Router>
              <Navbar />
              <ToastContainer
                newestOnTop
                toastStyle={{ backgroundColor: "var(--clrbackground)", color: "var(--clrdark)" }} />
              <LoginPopup />
              <SignupPopup />
              <Routes>
                <Route exact path="/" element={<Home />} />
                <Route exact path="/discussions" element={<Sections />} />
                <Route exact path="/privacypolicy" element={<PrivacyPolicy />} />
                <Route exact path="/discussions/:category" element={<Discussions />} />
                <Route path="/projects" element={<Projects />} />
                <Route path="/tutorials" element={<Tutorials />} />
                <Route exact path="/discussions/d/:id" element={<Post />} />
                <Route exact path="/u/profile/:id" element={<Profile />} />
                <Route exact path="/admin" element={<AdminPanel />} >
                  <Route path='/admin/allusers' element={<AllUsers />} />
                  <Route path='/admin/approveposts/:categ' element={<ApprovePosts />} />
                </Route>
                <Route path="*" element={<Navigate to="/" replace />} />
              </Routes>
              <Footer />
            </Router>
            </IPcontextState>
          </UserContextState>
        </LoginSignupPopupState>
      </IsLoggedInState>
    </>
  );
}

export default App;
