import React, { useState, useContext, useEffect } from "react";
import "../styling/Navbar.css";
import { Link, NavLink } from "react-router-dom";
import LoginSignupPopupContext from "../contexts/LoginSignupPopupContext";
import icon4 from "../icons/icon4.png";
import IsLoggedInContext from '../contexts/IsLoggedInContext'
import httpClient from '../httpClient';
import { useUser } from "../contexts/UserContext";
import { useIP } from "../contexts/IpContext";

import axios from "axios";
import { toast } from "react-toastify";

export default function Navbar() {
  const [searchBar, setSearchBar] = useState(false);
  const [dropDown, setDropDown] = useState(false);
  const popupsState = useContext(LoginSignupPopupContext);
  const loginContext = useContext(IsLoggedInContext);
  const { ip } = useIP();
  const { user, setUser } = useUser();
  const [searches, setSearches] = useState("");
  const [searchInput, setSearchInput] = useState("");
  const isLoggedIn = window.localStorage.getItem("isLoggedIn");
  const theme = window.localStorage.getItem("darkmode");
  const [NavbarForLargeDevices, setNavbarForLargeDevices] = useState(false);
  const [openMenu, setOpenMenu] = useState(false);
  const [darkTheme, setDarkTheme] = useState("");


  const enableLightTheme = () => {
    document.documentElement.style.setProperty('--clrprimary', 'rgb(228, 5, 5)')
    document.documentElement.style.setProperty('--clrprimarydark', 'red')
    document.documentElement.style.setProperty('--clrbackground', 'white')
    document.documentElement.style.setProperty('--clrdivcolor', 'white')
    document.documentElement.style.setProperty('--clrdark', '#3f3e3e')
    document.documentElement.style.setProperty('--clrnavbarborder', '#e5e5e5')
    document.documentElement.style.setProperty('--clrnavlinkactive', '#fff1f0')
    document.documentElement.style.setProperty('--clrdropdownhover', 'rgb(240, 239, 239)')
    document.documentElement.style.setProperty('--clrwelcomecontainer', 'rgb(228, 5, 5)')
    document.documentElement.style.setProperty('--clrdivseperatebar', '#fafafa')
    document.documentElement.style.setProperty('--clrtemplatebg', '#fafafa')
    document.documentElement.style.setProperty('--clrfootercolor', '#dfdfdf')
    window.localStorage.setItem("darkmode", false);
  }

  const enableDarkTheme = () => {
    // document.documentElement.style.setProperty('--clrprimary', 'rgb(199, 114, 3)')
    // document.documentElement.style.setProperty('--clrprimarydark', 'rgb(170, 97, 2)')
    document.documentElement.style.setProperty('--clrbackground', '#161c22')
    document.documentElement.style.setProperty('--clrdivcolor', '#1b2028')
    document.documentElement.style.setProperty('--clrdark', 'rgb(233, 233, 233)')
    document.documentElement.style.setProperty('--clrnavbarborder', '#161c22')
    document.documentElement.style.setProperty('--clrnavlinkactive', '#161c22')
    document.documentElement.style.setProperty('--clrdropdownhover', '#161c22')
    // document.documentElement.style.setProperty('--clrwelcomecontainer', 'rgb(170, 97, 2)')
    document.documentElement.style.setProperty('--clrwelcomecontainer', 'rgb(192, 3, 3)')
    document.documentElement.style.setProperty('--clrdivseperatebar', '#1b2028')
    document.documentElement.style.setProperty('--clrtemplatebg', '#14191f')
    document.documentElement.style.setProperty('--clrfootercolor', '#1b2028')
    window.localStorage.setItem("darkmode", true);
  }
  const getUser = async () => {
    if (isLoggedIn) {
      try {
        const res = await httpClient.get(`${ip}/profile/${localStorage.getItem("session_id")}`);
        console.log(("TEST"));
        if (res.status === 200) {
          setUser(res.data) && setUser(res.data);
          res.data.role === "user" ? window.localStorage.setItem("currentAdmin", false) : window.localStorage.setItem("currentAdmin", true);
          if (res.data.role === "admin") {
            loginContext.setAdminLoggedIn(true);
          }
        }
        else {
          console.log("Not authenticated, 401")
          localStorage.removeItem("isLoggedIn")
          toast.error("Please login again to continue")
        }
      } catch (error) {
          toast.error("Please login again to continue")
          localStorage.removeItem("isLoggedIn")
          setTimeout(() => {
            window.location.reload();
          }, 2500);
          console.log(error);
      }
    }
  };

  const search_posts = async () => {
    if (searchInput) {
      try {
        const res = await axios.get(`${ip}/search_post?search=${searchInput}`);
        setSearches(res.data);
      } catch (error) {
        // alert(error);
      }
    }
    else {
      setSearches("");
    }
  };

  const handleResize = () => {
    if (window.innerWidth > 520) {
      setNavbarForLargeDevices(true);
      if (window.innerWidth > 950) {
        setOpenMenu(true);
      }
    }
  }

  const handleClickOpenMenu = () => {
    setOpenMenu(!openMenu)
  }

  const initf = async () => {
    try {

      if (theme && theme === "true") {
        setDarkTheme(true)
      }
      else {
        setDarkTheme(false)
      }

      if (isLoggedIn && isLoggedIn === "true") {
        loginContext.setIsLoggedIn(true);
      }
      else {
        loginContext.setIsLoggedIn(false);
      }

    } catch (error) {
      console.log(error);
    }
  };

  const toggleSearch = () => {
    if (searchBar === true) {
      setSearchBar(false);
      setSearchInput("");
      setSearches("");
    } else {
      setSearchBar(true);
    }
  };

  const toggleDropDown = () => {
    if (dropDown === true) {
      setDropDown(false);
    } else {
      setDropDown(true);
    }
  };

  const logoutUser = async () => {
    await httpClient.post(`${ip}/logout/${localStorage.getItem("session_id")}`);
    window.localStorage.removeItem("isLoggedIn");
    window.localStorage.removeItem("session_id");
    window.localStorage.setItem("currentAdmin", false);
    window.location.href = "/";
  };

  useEffect(() => {
    const delayDebounceFn = setTimeout(() => {
      search_posts()
    }, 1000)

    return () => clearTimeout(delayDebounceFn)
  }, [searchInput])

  useEffect(() => {
    if (darkTheme === false) {
      enableLightTheme()
    }

    else if (darkTheme === true) {
      enableDarkTheme()
    }
  }, [darkTheme])

  useEffect(() => {
    getUser();
    initf();
    handleResize();
    window.addEventListener("resize", handleResize, false);
    return () => {
      window.scrollTo(0, 0);
    }
  }, []);

  return (
    <>
      <nav>
        <div className="NavbarContainer">
          <div className="Navbar">


            <div className="brandAndLinks">
              <div className="navBranding">
                <div className="navLogo">
                  <i className="faRed fa1 fa fa-thin fa-chevron-up"></i>
                  <i className="fa fa2 fa-thin fa-chevron-up"></i>
                  <i className="faRed fa3 fa fa-thin fa-chevron-up"></i>
                  <Link className="bgNone" to="/">
                    <div className="navTitle">StackNET</div>
                  </Link>
                </div>
              </div>
              <ul className="navLinks" style={openMenu ? { transform: 'translateY(5%)' } : { transform: 'translateY(-100%)' }}>
                <li>
                  <NavLink to="/">
                    Home
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/discussions">
                    Discussions
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/tutorials">
                    Tutorials
                  </NavLink>
                </li>
                <li>
                  <NavLink to="/projects">
                    Projects
                  </NavLink>
                </li>
                <li className="bgNone" onClick={toggleSearch}>
                  <button className="searchBtn">
                    <i className="fa-solid fa-magnifying-glass"></i>
                  </button>
                </li>
                {(!NavbarForLargeDevices && !loginContext.IsLoggedIn) && <div className="authBtnOuters">
                  <button
                    className="loginBtn"
                    onClick={() => popupsState.setOpenTheLoginPopup(true)}
                  >
                    Login
                  </button>
                  <button
                    className="Btn"
                    onClick={() => popupsState.setOpenTheSignupPopup(true)}
                  >
                    Register
                  </button>
                </div>}
              </ul>
            </div>

            {loginContext.IsLoggedIn ? (

              <div style={{ display: 'flex', alignItems: 'center' }}>

                <div className="darkModeDiv">
                  <i className="fas fa-sun"></i>
                  <label className="switch">
                    <input type="checkbox" className="darkmodeBtn" checked={darkTheme} onClick={() => setDarkTheme(!darkTheme)} />
                    <span className="slider round"></span>
                  </label>
                  <i className="fas fa-moon"></i>
                </div>

                <div className="PanelBtnNProfile">
                  <div className="profile" onClick={toggleDropDown}>
                    <img src={user.uimg ? ("data:image/jpg;base64," + user.uimg) : icon4} alt="" />
                    {dropDown && (
                      <div className="dropdown">
                        <Link to={`/u/profile/${user.id}`}>
                          <div className="sub-item">
                            <i
                              className="fa-regular fa-user"
                              style={{ color: "var(--clrdark)", marginLeft: "1px" }}
                            ></i>
                            Profile
                          </div>
                        </Link>
                        {loginContext.adminLoggedIn &&
                          <Link to="/admin">
                            <div className="sub-item">
                              <i
                                className="fa-sharp fa-solid fa-screwdriver-wrench"
                                style={{ color: "var(--clrdark)", marginLeft: "1px" }}
                              ></i>
                              Dashboard
                            </div>
                          </Link>
                        }
                        <div className="sub-item" onClick={logoutUser}>
                          <i className="fa-sharp fa-solid fa-arrow-right-from-bracket"></i>
                          Logout
                        </div>
                      </div>
                    )}
                  </div>
                </div>
                <div className="hamburger" style={{ marginTop: "27px" }} onClick={() => handleClickOpenMenu()}>
                  <span className="hamburger-stripes"></span>
                  <span className="hamburger-stripes"></span>
                  <span className="hamburger-stripes"></span>
                </div>
              </div>
            ) : (
              <div className="navBtns">
                <div className="darkModeDiv">
                  <i className="fas fa-sun"></i>
                  <label className="switch">
                    <input type="checkbox" className="darkmodeBtn" checked={darkTheme} onClick={() => setDarkTheme(!darkTheme)} />
                    <span className="slider round"></span>
                  </label>
                  <i className="fas fa-moon"></i>
                </div>
                {NavbarForLargeDevices && <><button
                  className="loginBtn"
                  onClick={() => popupsState.setOpenTheLoginPopup(true)}
                >
                  Login
                </button>
                  <button
                    className="Btn"
                    onClick={() => popupsState.setOpenTheSignupPopup(true)}
                  >
                    Register
                  </button></>}

                <div className="hamburger" onClick={() => handleClickOpenMenu()}>
                  <span className="hamburger-stripes"></span>
                  <span className="hamburger-stripes"></span>
                  <span className="hamburger-stripes"></span>
                </div>
              </div>
            )}
          </div>
        </div>
        {searchBar && (
          <>
            <div className="SearchBar">
              <div className="searchIcon"><i className="fa-solid fa-magnifying-glass"></i></div>
              <input type="search" placeholder="Search" value={searchInput} onChange={e => { setSearchInput(e.target.value) }} />
            </div>
            {searches && (
              <div className="Searches">
                {searches.map((post) => {
                  return (
                    <Link to={`/discussions/d/${post.id}`} key={post.id} onClick={toggleSearch} >
                      {post.title}
                    </Link>
                  );
                })}
              </div>
            )}
          </>
        )}
      </nav>
    </>
  );
}
