import React, { useState } from "react";
import IsLoggedInContext from "./IsLoggedInContext"

const IsLoggedInState = (props) => {

  const [IsLoggedIn, setIsLoggedIn] = useState(true); // false tha tu wo issue aara tha login wala
  const [adminLoggedIn, setAdminLoggedIn] = useState(false); // false tha tu wo issue aara tha login wala

  return (
    <IsLoggedInContext.Provider value={{ IsLoggedIn, setIsLoggedIn, adminLoggedIn, setAdminLoggedIn }}>
      {props.children}
    </IsLoggedInContext.Provider>
  );
};

export default IsLoggedInState;
