import { createContext, useContext, useState } from "react";

const IPcontext = createContext("");

export const IPcontextState = (props) => {

  const [ip, setIP] = useState("http://127.0.0.1:5000");

  return (
    <IPcontext.Provider value={{ ip, setIP }}>
      {props.children}
    </IPcontext.Provider>
  );
};

export const useIP = () => useContext(IPcontext);
