import React, {useEffect, useState} from 'react'
import PostIntro from './PostIntro'
import axios from 'axios';
import { useParams } from "react-router-dom";
import { useIP } from "../contexts/IpContext";


function ApprovePosts() {
  const [posts, setPosts] = useState("")
  const [changes, setChanges] = useState(0);
  const param = useParams();
  const { ip } = useIP();

  const getPosts = async () => {
    try {
      const res = await axios.get(`${ip}/get_pendingPosts?categ=${param.categ}`);
      setPosts(res.data);
    } catch (error) {
      // alert(error);
    }
  };
  useEffect(() => {
    getPosts();
  }, [param.categ])

  useEffect(() => {
    let newArr = posts && posts.filter(object => {
      return object.id !== changes;
    })
    setPosts(newArr)
  }, [changes])

  return (
    <>
      <h2 className='approvePostsTag'>{param.categ} pending for approval ({posts.length}) </h2>

      <div className='AllPosts'>
        <ul>
          {posts && posts.map((post) => {
            return (
                <li key={post.id}>
                  <PostIntro post={post} type={param.categ} changes={changes} setChanges={setChanges}/>
              </li>
            );
          })}
        </ul>
      </div>
    </>
  )
}

export default ApprovePosts