import axios from 'axios'
import React from 'react'
import '../styling/AdminPanel.css'
import UserCard from './UserCard'
import { useState, useEffect } from 'react'
import { useIP } from "../contexts/IpContext";

function AllUsers() {

    const [users, setUsers] = useState([])
    const [userDeleted, setUserDeleted] = useState(0)
    const { ip } = useIP();

    const getUsers = async () => {
        const res = await axios.get(`${ip}/get_users`);
        let { data } = res.data;
        setUsers(res.data);
    };
    useEffect(() => {
        getUsers();
    }, [])

    useEffect(() => {
        let newArr = users.filter(object => {
          return object.id !== userDeleted;
        })
        setUsers(newArr)
      }, [userDeleted])

    return (
        <>
            <h2 className='approvePostsTag'>Registered Users ({users.length}) </h2>
            <div className='AllUsers'>
                {users.map(user => {
                    return (
                        <div className='userCard' key={user.id}>
                        <UserCard user={user} userDeleted={setUserDeleted}/>
                        </div>)
                })}

            </div>
        </>
    )
}

export default AllUsers